.select {
    grid-area: input;
    width: 300px;
    height: 30px;
    box-sizing: border-box;
    resize: both;
    border-color: darkgray;
    border-radius: 5px;
}

.select:focus {
    outline: 0.15em solid dodgerblue;
    background: transparent;
}


.select .option:hover {
    background: dodgerblue ;
}
