[data-theme='1'] {
    --color: #000000;
    --background: #FFFFFF;
}

[data-theme='2'] {
    --color: #000000;
    --background: #DDEEFF;
}

[data-theme='3'] {
    --color: #FFFFFF;
    --background: #212A34;
}

html {
    background-color: var(--background);
    color: var(--color);
}

.select {
    grid-area: input;
    width: 300px;
    height: 30px;
    box-sizing: border-box;
    resize: both;
    border-color: darkgray;
    border-radius: 5px;
}
